  align-items: center;
  flex-direction: column;
  justify-content: center;
  transition: all 0.2s ease-in;
  border: 1px solid #eaeaea;
  padding: 2em 0 1em 0;
}

.box_3_active_trainer .dd_row_two .option .option_wapper img {
  width: 54px;
  position: relative;
  left: 0px;
  top: -7px;

  height: 60px;
}

.box_3_active_trainer .dd_row_two .option .option_wapper h2 {
  padding-top: 0.5em;
  width: 140px;
  height: 16px;

  text-align: center;

  letter-spacing: 0.7px;
